import {WinJSDec, fnError, appError} from './APP/config';

var name = 'JUMP_BACK';
var JUMP_BACK = (id, fn=_=>{}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('address', 'chooseAddress', {id}, res => {
        var {code, response} = res;
        if (code == 0) {
          fn(response);
        } else {
          throw new Error(`[${name}] ${appError}`);
        }
      });
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default JUMP_BACK;
